var div = document.createElement('div')
div.style.border = '20px solid red'
div.style.height = '200px'
div.style.width = '200px'
div.id = 'demo'

document.body.appendChild(div)

var flag = false
var postion = [0,0]
document.onmousedown = (e)=>{
    flag = true
    postion = [e.clientX,e.clientY]
}

div.onmousemove = (e)=>{
    console.log(e.clientX,e.clientY)
    if(flag === true){  
    var deltaX = e.clientX - postion[0]
    var deltaY = e.clientY - postion[1]
    var top = parseInt(div.style.top!) ||0
    var left = parseInt(div.style.left!) ||0

    div.style.top = top+deltaY+'px'
    div.style.left = left+deltaX+'px'
    postion = [e.clientX,e.clientY]
}
}

document.onmouseup=(e)=>{
flag=false
}